
<template>
  <div class="player">
    <div class="plays"></div>
    <!-- <p>我在这里{{$route.params.id}}</p> -->
    <div class="top">
      <div style="width:2rem;" @click="histo">
        <img style="width:100%;" v-lazy="require('../../assets/icon/bottom.png')" alt />
      </div>
      <span>{{list.song}}</span>
      <div style="width:2rem;">
        <img style="width:100%;" v-lazy="require('../../assets/icon/sandian.png')" alt />
      </div>
    </div>
    <div>
      <p class="www">{{list.sname}}</p>
    </div>
    <!-- 照片 -->
    <div class="pic">
      <img class="pic_img" :style="cc1" v-lazy="'http://127.0.0.1:3000'+list.simg" alt />
    </div>
    <div class="body">
      <p>{{start}}:{{start1}}{{start2}}</p>
      <div>
        <i class="i" :style="{animationPlayState:run,animationDuration:timer}"></i>
        <p class="p"></p>
      </div>
      <p>{{end}}</p>
    </div>
    <!-- 暂停播放按钮 -->
    <div class="bar">
      <div style="width:2rem;">
        <img style="width:100%;" src="../../assets/icon/xunhuan.png" alt />
      </div>
      <div style="width:2rem;" @click="to1(list.id)">
        <img style="width:100%;" src="../../assets/icon/left.png" alt />
      </div>
      <div style="width:3rem;" @click="paused">
        <img style="width:100%;" :src="src" alt />
      </div>
      <div style="width:2rem;" @click="to2(list.id)">
        <img style="width:100%;" src="../../assets/icon/right.png" alt />
      </div>
      <div style="width:1.5rem;">
        <img style="width:100%;" src="../../assets/icon/guanli.png" alt />
      </div>
    </div>
    <div class="last">
      <div style="width:2rem;" @click="bianse(list.id)">
        <img style="width:100%;" :src="src1" alt />
      </div>
      <div style="width:2rem;">
        <img style="width:100%;" src="../../assets/icon/xiazai.png" alt />
      </div>
      <div style="width:2rem;">
        <img style="width:100%;" src="../../assets/icon/fenxiang.png" alt />
      </div>
      <div style="width:2rem;">
        <img style="width:100%;" src="../../assets/icon/xiaoxi.png" alt />
      </div>
    </div>
    <!-- 这里用来写音乐播放器 -->
    <div class="btyy">
      <audio id="aud" class="audio" autoplay :src="'http://127.0.0.1:3000'+list.songpath"></audio>
    </div>
    <div>
      <div class="show" :class="s">
        <img src="../../assets/icon/hongse.png" alt />
      </div>
    </div>
  </div>
</template>

<script>
// import { clearInterval } from 'timers';
export default {
  data() {
    return {
      // paused暂停；running播放    animation-play-state:running;
      cc1: "animation-play-state:running;",
      run: "running",
      timer: "240s",
      // cc2:"-webkit-animation-duration:5s",
      src: require("../../assets/icon/zanting.png"),
      src1: require("../../assets/icon/huise.png"),
      isrun: true,
      islove: false,
      list: [],
      start: 0,
      start1: 0,
      start2: 0,
      end: "",
      clear1: null,
      clear2: null,
      clear3: null,
      s: ""
    };
  },
  created() {
    this.get();
  },
  beforeMount() {
    this.gettt();
    this.gettime();
  },
  methods: {
    gettt() {
      this.clear1 = setTimeout(() => {
        // this.$nextTick(function(){
        // await this.$nextTick()  =>es6的语法
        let music = document.getElementById("aud");
        let timelong = music.duration;
        let time = parseInt(timelong);
        // 这是视频的长度，用于绑定过渡动画的时间
        this.timer = time + "s";
        // (()=>{
        var h;
        h = Math.floor(time / 60);
        time = time % 60;
        h += "";
        time += "";
        h = h.length == 1 ? "0" + h : h;
        time = time.length == 1 ? "0" + time : time;
        this.end = h + ":" + time;
        // })

        // })()
      }, 300);
    },
    gettime() {
      var s = this.start2;
      var s1 = this.start1;
      var s2 = this.start;
      this.clear2 = setInterval(() => {
        s++;
        if (s == 10) {
          s = 0;
          s1++;
          this.start1 = s1;
          this.start2 = 0;
          // return;
        }
        if (s1 == 6) {
          s = 0;
          s1 = 0;
          s2++;
          this.start = s2;
          this.start1 = 0;
          this.start2 = 0;
          // return;
        }
        this.start2 = s;
      console.log(this.start2);
      }, 1000);
    },
    bianse(id) {
      if (this.islove == false) {
        window.sessionStorage.setItem("love", [id]);
        // var a= window.sessionStorage.getItem("love");
        // console.log(a)
        this.src1 = require("../../assets/icon/hongse.png");
        this.s = "show1";
        setTimeout(() => {
          this.s = "";
        }, 3000);
        this.islove = true;
      } else {
        this.src1 = require("../../assets/icon/huise.png");
        this.islove = false;
      }
    },
    paused() {
      var id = document.getElementById("aud");
      if (this.isrun == true) {
        this.src = require("../../assets/icon/bofang.png");
        this.cc1 = "animation-play-state:paused;";
        this.cc = "animation-play-state:paused;";
        id.pause();
        clearInterval(this.clear2);
        this.isrun = false;
      } else {
        this.gettime();
        this.src = require("../../assets/icon/zanting.png");
        this.cc1 = "animation-play-state:running;";
        this.cc = "animation-play-state:running;";
        id.play();
        this.isrun = true;
      }
    },
    histo() {
      this.$router.push("/search");
    },
    get() {
      //从music发送数据
      let id = this.$route.params.id;
      let url = "/musiclist";
      let params = { id };
      this.axios.get(url, { params }).then(res => {
        this.list = res.data[0];
      });
    },
    to1(id) {
      let gid = --id;
      this.$router.push("/player/" + gid);
      this.get();
      this.gettt();
      this.start2 = 0;
    },
    to2(id) {
      let gid = ++id;
      this.$router.push("/player/" + gid);
      this.get();
      this.gettt();
      this.start2 = 0;
    }
  },
  beforeDestroy() {
    clearTimeout(this.clear1);
    clearInterval(this.clear2);
  }
};
</script>

<style scoped>
.audio {
  width: 3rem;
  height: 3rem;
  position: fixed;
  bottom: 4.8rem;
  left: 11.5rem;
  z-index: -5;
}
.plays {
  width: 100%;
  height: 100%;
  position: fixed;
  /* background: url(../../assets/lingsui/first_gexing_right.jpg) no-repeat; */
  background: linear-gradient(
    to bottom,
    hsl(108, 91%, 50%),
    rgb(127, 10, 223),
    rgb(231, 30, 30),
    rgb(235, 9, 28)
  );
  background-size: 100% 100%;
  z-index: -1;
  opacity: 1;
  /* filter:blur(0px); */
}
.top {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.www {
  position: relative;
}
.www::before {
  content: "";
  position: absolute;
  border: 0.9px solid #aaa;
  width: 2rem;
  top: 0.5rem;
  left: 7.5rem;
}
.www::after {
  content: "";
  position: absolute;
  border: 0.9px solid #aaa;
  width: 2rem;
  top: 0.5rem;
  left: 14.2rem;
}
.pic {
  width: 100%;
  padding: 3rem;
  border-radius: 50%;
}
.pic_img {
  border-radius: 50%;
  width: 100%;
  animation: b 30s linear infinite alternate;
}
@keyframes b {
  0% {
    transform: translate(0px, 0px) rotate(0deg);
  }
  100% {
    transform: translate(0px, 0px) rotate(360deg);
  }
}
.body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  position: relative;
}
.i {
  position: absolute;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  left: 3.2rem;
  bottom: 1.7rem;
  background-color: #0f0;
  z-index: 2;
  animation-name: a;
  animation-timing-function: linear;
  animation-duration: 240s;
}

@keyframes a {
  0% {
    transform: translate(0px, 0px) rotate(0deg);
  }
  100% {
    transform: translate(300px, 0px) rotate(0deg);
  }
}
/* animation-play-state:paused; */
.p {
  content: "";
  position: absolute;
  width: 71%;
  left: 3.6rem;
  bottom: 1.5rem;
  border: 1px solid #aaa;
}

.bar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  /* background-color:#fff; */
  border-bottom: 1px solid #ccc;
  padding: 1rem;
  margin-top: 1rem;
}
.last {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  /* background-color: #fff; */
  margin-top: 1rem;
}
.show {
  width:0;
  height:0;
  top: 30%;
  left: 30%;
  position: absolute;
  border-radius: 50%;
  opacity: 0;
  transition: all 2s linear;
}
.show1 {
  width:0;
  height:0;
  top: 30%;
  left: 30%;
  opacity: 0.8;
}
img[lazy=loading] {
  width: 100%;
  height: 100px;
  margin: auto;
}
</style>
